img{
    max-width: 90%;
    margin-top : 10px;
}


.product {
    display: inline-block;
    margin-left: 1.5%;
    max-width: 22.5%;
    padding : 4px;
    margin-top: 4%;
    border: 1px solid black;
    text-align : center;
}

.product:hover{
    background-color: rgb(250, 208, 240);
    
}


.delete-button {
    background-color: rgb(255, 72, 0);
    color: white;
    border: none;
    float : right;
    height : 40px;
    width : 120px;
    border-radius: 4px;
    margin-right: 10px;
    margin-bottom : 10px;
    text-align : center;
  }

.delete-button:hover{
    background-color: red;
    color: black;
    cursor: pointer;

  }

  .add-button {
    background-color: rgb(0, 255, 136);
    color: white;
    border: none;
    float : left;
    height : 40px;
    width : 120px;
    margin-left: 10px;
    border-radius: 4px;
    margin-bottom: 10px;
    text-align: center;
  }

.add-button:hover{
    background-color: rgb(60, 145, 7);
    color: black;
    cursor: pointer;

  }